import './App.css';
import { ethers } from 'ethers';
import React, { useState, useEffect } from 'react';
import IERC20ABI from './artifacts/contracts/StakeETN.sol/IERC20.json';
import StakeETNABI from './artifacts/contracts/StakeETN.sol/StakeETN.json';
import copy from 'copy-to-clipboard';
import logo from './assets/img/favicon.png';
import { message } from 'antd';

let Mainnet = true;
let name = "ETN"
let chainINFO;
let Provider;
let StakeETN;
let account;
let ContractToken;
let StakeETNAddr = "0x57F77eCdAEF5178Aafafe1656b590E2009781aDe";
let ContractTokenAddr = "0x2f38a08A893b122d0006Bd5865b77a4208f9Ea3e";
let tokenDecimals = Math.pow(10, 18);
let balanceDeciaml = 6;
let baseUrl = "http://pl.etnhome.pro/"
let RefAccount;
let ChainID;
let timer1;

export default function App() {
  let [Account, setAccount] = useState("连接钱包");
  let [Token, setToken] = useState("--");
  let [refURL, setrefURL] = useState(baseUrl + "?ref=0x0000000000000000000000000000000000000000");
  let [StakeValue, setStakeValue] = useState("0");
  let [PlayerInfo, setPlayerInfo] = useState([false, 0, 0, 0]);
  let [PledgeProfit, setPledgeProfit] = useState("0");
  let [RefInfo, setRefInfo] = useState(["0", "0"]);
  let [ButtonInfo, setButtonInfo] = useState("质押");
  let [PoolBalance, setPoolBalance] = useState("--");
  let [Time, setTime] = useState([0, 0, 0, 0]);

  function isMetaMaskInstalled() {
    const { ethereum } = window;
    return Boolean(ethereum && ethereum.isMetaMask);
  }

  let GetAccount = async () => {
    try {
      await window.ethereum.request({ method: 'eth_requestAccounts' })
        .then((accounts) => {
          account = accounts[0];
        }).catch((error) => {
          console.error(error)
        });
      await getGameinfo();
      setrefURL(baseUrl + "?ref=" + account)
    } catch (err) {
      console.log(err)
    }

  }

  let ConnWeb3 = async () => {
    try {
      if (isMetaMaskInstalled()) {
        if (Provider && StakeETN && ContractToken && ChainID == chainINFO.chainId) {
          return
        }
        Provider = new ethers.providers.Web3Provider(window.ethereum);
        StakeETN = new ethers.Contract(StakeETNAddr, StakeETNABI.abi, Provider.getSigner());
        ContractToken = new ethers.Contract(ContractTokenAddr, IERC20ABI.abi, Provider.getSigner());
        await GetAccount();
        chainINFO = await Provider.getNetwork();
        console.log(account);
        await chainChanged();
        Provider.on('block', async (blockNumber) => {
          await getGameinfo();
        });
      }
    } catch (err) {
      Provider = new ethers.providers.Web3Provider(window.ethereum);
      chainINFO = await Provider.getNetwork();
      if (ChainID != chainINFO.chainId) {
        setAccount("错误的网络");
      }
      console.log(err)
    }
  }

  let chainChanged = async () => {
    try {
      if (Mainnet) {
        ChainID = 131419;
        if (chainINFO.chainId !== 131419) {
          setAccount("错误的网络");
          await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: '0x2015b' }],
          });

          await window.ethereum.request({
            method: 'wallet_addEthereumChain',
            params: [
              {
                chainId: '0x2015b',
                chainName: 'ETND Mainnet',
                rpcUrls: ['https://rpc.node1.etnd.pro'],
                
              },
            ],
          });
        } else {
          setAccount(account.substring(0, 10) + '...' + account.substring(33, 42).toString());
        }
      } else {
        ChainID = 5;
        if (chainINFO.chainId !== 5) {
          setAccount("错误的网络");
          await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: '0x2015b' }],
          });
        } else {
          setAccount(account.substring(0, 10) + '...' + account.substring(33, 42).toString());
        }
      }
    } catch (err) {
      Provider = new ethers.providers.Web3Provider(window.ethereum);
      chainINFO = await Provider.getNetwork();
      if (ChainID != chainINFO.chainId) {
        setAccount("错误的网络");
      }
      console.log(err)
    }
  }

  let getGameinfo = async () => {
    try {
      //console.log(account);
      let balance = await ContractToken.balanceOf(account);
      setToken((balance.toString() / tokenDecimals).toFixed(balanceDeciaml));
      await getPlayerInfos();
      await GetPledgeProfit();
      await Refinfo();
      let value_ = await ContractToken.allowance(account, StakeETNAddr);
      let _poolbalance = await ContractToken.balanceOf(StakeETNAddr);
      setPoolBalance((_poolbalance.toString() / tokenDecimals).toFixed(balanceDeciaml));
      //setAccount(account.substring(0, 10) + '...' + account.substring(33, 42).toString());
      //console.log("value_",value_)
      if (value_ < 1) {
        setButtonInfo("授权");
      } else {
        setButtonInfo("质押");
      }
    } catch (err) {
      Provider = new ethers.providers.Web3Provider(window.ethereum);
      chainINFO = await Provider.getNetwork();
      if (ChainID != chainINFO.chainId) {
        setAccount("错误的网络");
      }
      console.log(err);
    }
  }

  let Stakes = async () => {
    if (!Provider && !StakeETN && !ContractToken) {
      message.warning('请先连接钱包', 0.5);
      return
    }
    if (StakeValue == 0) {

      return
    }
    //console.log("StakeValue:",StakeValue);
    let wei = ethers.utils.parseEther(StakeValue);
    //console.log(wei.toString(10));
    alert("注意:重复质押会使赎回期重置为180天");
    await StakeETN.Stake(RefAccount, wei.toString(10));
  }

  let Withdrawal = async () => {
    if (!Provider && !StakeETN && !ContractToken) {
      message.warning("请先连接钱包", 0.5);
      return
    }
    alert("注意:赎回期前提现本金将扣除30%本金作为处罚");
    await StakeETN.withdrawal();
  }

  let Profit = async () => {
    if (!Provider && !StakeETN && !ContractToken) {
      message.warning("请先连接钱包", 0.5);
      return
    }
    //显示提示框信息为  频繁点击领取收益按钮会导致重置收益领取时间
    await StakeETN.getProfit();
  }

  let GetPledgeProfit = async () => {
    let _PledgeProfit = await StakeETN.getPledgeProfit(account);
    setPledgeProfit((_PledgeProfit / tokenDecimals).toFixed(balanceDeciaml));
  }

  let Refinfo = async () => {
    let [_Peoples, _sumvalue] = await StakeETN.refinfo(account);
    setRefInfo([_Peoples.toString(), _sumvalue.toString()]);
  }

  let getrefaddr = () => {
    let url = window.location.href;
    let urlArr = url.split('ref=');
    if (urlArr.length > 1) {
      RefAccount = urlArr[1].slice(0, 42)
    } else {
      RefAccount = "0x0000000000000000000000000000000000000000";
    }
  }

  let getPlayerInfos = async () => {

    let [_isRegister, _PledgeTime, _WithdrawTime, _PledgeAmount] = await StakeETN.getPlayerInfo(account);
    setPlayerInfo([_isRegister, _PledgeTime.toString(), _WithdrawTime.toString(), _PledgeAmount.toString()]);
    if (_WithdrawTime > 0) {
      clearInterval(timer1);
      timer1 = setInterval(async function () {
        let now = parseInt(Date.now() / 1000);
        let day = parseInt((_WithdrawTime.toString() - now) / 86400);
        let hour = parseInt((_WithdrawTime.toString() - now) % 86400 / 3600);
        let minute = parseInt((_WithdrawTime.toString() - now) % 86400 % 3600 / 60);
        let second = parseInt((_WithdrawTime.toString() - now) % 86400 % 3600 % 60);
        console.log([day, hour, minute, second]);
        setTime([day, hour, minute, second]);
      }, 1000);
    }else{
      setTime([0, 0, 0, 0]);
      clearInterval(timer1);
    }

    console.log(_isRegister, _PledgeTime.toString(), _WithdrawTime.toString(), _PledgeAmount.toString());
  }


  useEffect(() => {
    getrefaddr();
    window.ethereum.on('accountsChanged', async function () {
      await GetAccount();
      await chainChanged();
      console.log("accountsChanged:", account);
      //setAccount(account.substring(0, 10) + '...' + account.substring(33, 42).toString());
    })
    window.ethereum.on('chainChanged', async function () {
      console.log("chainChanged")
      await chainChanged();
      await ConnWeb3();
      Provider = new ethers.providers.Web3Provider(window.ethereum);
      chainINFO = await Provider.getNetwork();
      if (ChainID != chainINFO.chainId) {
        setAccount("错误的网络");
      }
      //setAccount(account.substring(0, 10) + '...' + account.substring(33, 42).toString());
    });

  }, [])

  let ShowAccountbutton = () => {
    return <button className="button button-danger" onClick={ConnWeb3}>{Account}</button>
  }

  let ShowPoolBalance = () => {
    return <div className="balance-box d-flex flex-column align-items-center justify-content-center">
      <p>ETN池子</p>
      <p>
        <span className="text-bold ml-2 color-warning">{PoolBalance} {name}</span>
      </p>
    </div>
  }

  let ShowTokenBalance = () => {
    return (<div className="balance-box d-flex flex-column align-items-center justify-content-center">
      <p className="text-bold">您的 {name}余额</p>
      <p className="text-bold ml-2  text-sub mt-3 color-warning">{Token} {name}</p>
    </div>)
  }

  let ShowRefinfo = () => {
    return (
      <div >
        <input type="text" readOnly value={refURL}
          onClick={() => {
            if (!Provider && !StakeETN && !ContractToken) {
              message.warning("请先连接钱包", 0.5);
              return
            }
            copy(refURL);
            message.success("复制成功")
          }} />
      </div>
    )
  }

  let ShowStakeButton = () => {
    return (
      <div className="d-flex flex-column justify-content-center align-items-center">
        <div className="w-100 d-flex align-items-center">
          <input className="flex-grow-1" placeholder="最小质押1枚" value={StakeValue} type="number" onChange={(event) => {
            console.log(event.target.value);
            setStakeValue(event.target.value)
          }} />
          <button className="flex-shrink-0 ml-3 button button-primary plain" onClick={async () => {
            if (!Provider && !StakeETN && !ContractToken) {
              message.warning("请先连接钱包", 0.5);
              return
            }
            //要求后点击自动填写最大值maxvalue
            let balance = await ContractToken.balanceOf(account);
            let maxvalue = (balance.toString() / tokenDecimals).toFixed(18);
            setStakeValue(maxvalue.toString());

          }}>最大</button>
        </div>
        <div className="mt-3">
          <button className="button button-primary linear" onClick={async () => {
            if (ButtonInfo == "质押") {
              await Stakes()
            } else {
              let _totalSupply = await ContractToken.totalSupply();
              await ContractToken.approve(StakeETNAddr, _totalSupply);
              //setButtonInfo("质押");
            }
          }}>{ButtonInfo} {name} Token</button>
        </div>
      </div>
    )
  }

  let ShowWithdrawalButton = () => {
    return (
      <div className="d-flex flex-column justify-content-center align-items-center">
        <p className="d-flex align-items-center line-height">
          <span>您已质押的{name}</span>
        </p>
        <p>
          <span className="text-bold ml-2 color-warning">{(PlayerInfo[3].toString() / tokenDecimals).toFixed(balanceDeciaml)} {name}</span>
        </p>
        <p>
          {Time[0] + "天" + Time[1] + "小时" + Time[2] + "分" + Time[3] + "秒" + "后可提取"}
        </p>
        <div className="mt-3">
          <button className="button button-warning linear" onClick={Withdrawal}>解除质押 {name} Token</button>
        </div>
      </div>
    )
  }

  //领取收益按钮
  let ShowgetProfitButton = () => {
    return (
      <div className="d-flex flex-column justify-content-center align-items-center">
        <p className="d-flex align-items-center line-height">
          <span>可领取收益</span>
        </p>
        <p>
          <span className="text-bold ml-2 color-warning">{PledgeProfit} {name}</span>
        </p>
        <div className="mt-3">
          <button className="button button-warning linear" onClick={Profit}>提现 {name} Token</button>
        </div>
      </div>
    )
  }

  let ShowRefinfos = () => {
    return (
      <div>
        <div className="d-flex align-items-center line-height">
          <p>邀请人数</p>
          <p className="text-bold ml-2 color-warning">{RefInfo[0]}</p>
        </div>
        <div className="d-flex align-items-center line-height"></div>
      </div>

    )
  }

  return (
    <div className="app">
      <header className="d-flex align-items-center justify-content-center py-3">
        <ShowAccountbutton />
      </header>
      <main className="px-3">
        <div className="d-flex align-items-center justify-content-center py-2">
          <img src={logo} />
        </div>
        <section className="stake-box">
          <ShowPoolBalance />
        </section>
        <section className="stake-box">
          <ShowTokenBalance />
          <div className="d-flex flex-column justify-content-center align-items-center">
            <div className="w-100 d-flex align-items-center">
              <input className="flex-grow-1" placeholder="最小质押1枚" value={StakeValue} type="number" onChange={(event) => {
                console.log(event.target.value);
                setStakeValue(event.target.value)
              }} />
              <button className="flex-shrink-0 ml-3 button button-primary plain" onClick={async () => {
                if (!Provider && !StakeETN && !ContractToken) {
                  message.warning("请先连接钱包", 0.5);
                  return
                }
                let balance = await ContractToken.balanceOf(account);
                let maxvalue = (balance.toString() / tokenDecimals).toFixed(18);
                setStakeValue(maxvalue.toString());

              }}>最大</button>
            </div>
            <div className="mt-3">
              <button className="button button-primary linear" onClick={async () => {
                if (ButtonInfo == "质押") {
                  await Stakes()
                } else {
                  let _totalSupply = await ContractToken.totalSupply();
                  await ContractToken.approve(StakeETNAddr, _totalSupply);
                }
              }}>{ButtonInfo} {name} Token</button>
            </div>
          </div>
          {/* <ShowStakeButton /> */}
        </section>
        <section className="withdraw-box">
          <ShowWithdrawalButton />
        </section>
        <section className="profit-box">
          <ShowgetProfitButton />
        </section>
      </main>
      <footer className="px-3 pb-3 d-flex flex-column align-items-center justify-content-center">
        <ShowRefinfos />
        <ShowRefinfo />
      </footer>
    </div>
  );
}

